  font-weight: 700;
  line-height: 150%;
}

.header__link:after {
  content: "";
  transform-origin: 100% 100%;
  width: 100%;
  height: 1px;
  transition: transform .5s ease-out;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.header__link:hover:after {
  transform-origin: 0 100%;
  background-color: #2060f6;
  transform: scaleX(1);
}

.header__link--underline {
  color: #fff;
  letter-spacing: 3px;
  text-transform: uppercase;
